body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}
  
.row {
  display: flex;
  flex-wrap: wrap;
}
  
.block {
  display: flex;
  justify-content: center;
  color: #fff;
  height: 300px;
  align-items: center;
  flex-basis: 300px;
  flex-grow: 1;
  flex-shrink: 0;
}
  
.block__1 {
  background-color: #f00;
}
  
.block__2 {
  background-color: #c00;
}
  
.block__3 {
  background-color: #900000;
}
  
.block__4 {
  background-color: #600000;
}
  
.block__5 {
  background-color: #300000;
}
  
.block__6 {
  background-color: #000;
}
  
@media (min-width: 600px) {
  .block__3 {
    order: 1;
  }
  .block__5 {
    order: 1;
  }
  .block__6 {
    order: 1;
  }
}
  
@media (min-width: 900px) {
  .row {
    flex-direction: column;
    height: 600px;
  }
  .block__2 {
    order: 2;
  }
  .block__3 {
    order: 5;
  }
  .block__4 {
    order: 6;
  }
  .block__5 {
    order: 3;
  }
}
